import '../../src/App.scss'

import React, { useState, useEffect } from 'react';
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";
import type { Engine, Container } from "tsparticles-engine"; //  

const MainPage: React.FC = () => {
    const particlesInit = async (main: Engine): Promise<void> => {
        await loadFull(main);
    };

    const particlesLoaded = async (container: Container | undefined): Promise<void> => {
        console.log(container);
    };

    const [isDarkMode, setIsDarkMode] = useState(false);

    const toggleDarkMode = () => {
        setIsDarkMode(!isDarkMode);
    };

    useEffect(() => {
        document.documentElement.className = isDarkMode ? 'dark-mode' : 'normal-mode';
    }, [isDarkMode]);

    return (
        <div className='container'>
            <Particles
                id="tsparticles"
                init={particlesInit}
                loaded={particlesLoaded}
                options={{
                    background: {
                        color: {
                            value: isDarkMode ? "#000000" : '#ffffff', //  
                        },
                    },
                    fpsLimit: 60, //  
                    particles: {
                        number: {
                            value: 100, //   
                            density: {
                                enable: true,
                                value_area: 800, //    
                            },
                        },
                        color: {
                            value: isDarkMode ? "#ffffff" : "#000000", //  
                        },
                        shape: {
                            type: "circle", //  
                        },
                        opacity: {
                            value: 0.5, //  
                            random: false, //   
                        },
                        size: {
                            value: 3, //  
                            random: true, //   
                        },
                        links: {
                            enable: true, //    
                            distance: 150, //   
                            color: isDarkMode ? "#ffffff" : "#000000", //  
                            opacity: 0.4, //  
                            width: 1, //  
                        },
                        move: {
                            enable: true, //   
                            speed: 1.5, //   
                            direction: "none", //  
                            random: false, //   
                            straight: false, //   
                            out_mode: "bounce", //  
                            bounce: false, //    
                        },
                    },
                    interactivity: {
                        detect_on: "canvas", //  canvas 
                        events: {
                            onhover: {
                                enable: true, //   
                                mode: "grab", //    
                            },
                            onclick: {
                                enable: true, //   
                                mode: "push", //    
                            },
                            resize: true, //     
                        },
                        modes: {
                            grab: {
                                distance: 140, // grab   
                                line_linked: {
                                    opacity: 1, // grab    
                                },
                            },
                            push: {
                                quantity: 4, //     
                            },
                        },
                    },
                    retina_detect: true, //    
                }}
            />
            <div className="textBox">
                <h1 className='title' data-aos="fade-up">Web Publisher</h1>
                <p className='desc' data-aos="fade-up" data-aos-delay="300">Portfolio</p>
                <button onClick={toggleDarkMode}>
                    {isDarkMode ? "Light Mode" : "Dark Mode"}
                </button>
            </div>
        </div>
    );
};

export default MainPage;
